.header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 20px 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header .logo {
  position: relative;
  max-width: 80px;
}
.header ul {
  position: relative;
  display: flex;
}
.header ul li {
  list-style: none;
}
.header ul li a {
  text-decoration: none;
}